body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.square {
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 48px;
  padding: 0;
  text-align: center;
  width: 48px;
}

.dark-square{
  background-color: RGB(187,190,100);
}
  
.light-square{
  background-color: RGB(234,240,206);
}

.row{
  display: table;
}

.piece{
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 48px;
  padding: 0;
  text-align: center;
  width: 48px;
  position: absolute;
  cursor: -webkit-grab;
}

.dragging{
  cursor: -webkit-grabbing;
  border-color: black;
  border-style: solid;
  z-index: 2;
}